<template>
  <div style="display: inline-block;">
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-icon
          small
          class="mr-2"
          v-on="on"
          v-bind="attrs"
          @click.stop="start(cUniqueCode)"
        >mdi-currency-cny</v-icon>
      </template>
      <span>续费列表</span>
    </v-tooltip>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <!-- <v-card-title>
               续费列表
        </v-card-title>-->
        <v-card-text>
          <v-data-table
            :headers="headers"
            :items="desserts"
            sort-by="calories"
            class="elevation-1"
            :options.sync="options"
            :server-items-length="totalDesserts"
          >
            <template v-slot:item.enable="{ item }">{{item.enable ? '已启用': '已禁用'}}</template>
            <template v-slot:top>
              <v-toolbar flat color="white">
                <v-toolbar-title>续费列表</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>

                <AddRenewalInfo @addRenewal="getAddRenewalFromSon" :cUniqueCode="cUniqueCode" />
                <v-btn color="primary" dark class="mb-2 ml-2" @click="closeRenewal">返回</v-btn>
              </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
              <EditRenewalInfo
                :renewalId="item.id"
                @editRenewal="getEditRenewalFromSon"
                :companyUniqueCode="cUniqueCode"
              />

              <delete-hint icon="mdi-delete" :func="() => { return deleteOne(item.id) }"></delete-hint>
            </template>
            <template v-slot:no-data>
              <!-- <v-btn color="yellow" @click="initialize">Reset</v-btn> -->
              <small>暂无数据</small>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>


<script>
export default {
  data: () => ({
    dialog: false,
    companyUniqueCode: "",
    // 分页信息
    options: {},
    totalDesserts: 0,
    headers: [
      { text: "创建人", value: "creator" },
      { text: "创建时间", value: "createtime" },
      { text: "开始时间", value: "renewalTime" },
      { text: "到期时间", value: "expirationTime" },
      { text: "是否开启", value: "enable" },
      { text: "版本", value: "version" },
      { text: "操作", value: "actions", sortable: false }
    ],
    desserts: []
  }),

  props: {
    cUniqueCode: {
      type: String
    }
  },

  computed: {},

  watch: {
    // dialog(val) {
    //   val || this.close();
    // },

    options: {
      handler(val) {
        this.options = val;
        this.initialize(this.cUniqueCode);
      },
      deep: true
    }
  },

  // created() {
  //   this.initialize(cUniqueCode);
  // },

  methods: {
    initialize(cUniqueCode) {
      let postData = {
        pageNum: this.options.page,
        pageSize: this.options.itemsPerPage,
        companyUniqueCode: cUniqueCode
      };
      this.$http
        .post("/user/api/v1/record/renewalPageList", postData)
        .then(res => {
          this.totalDesserts = res.data.total;
          this.desserts = res.data.records;
        });
    },

    // addRenewalInfo(cUniqueCode) {
    //   this.$router.push({
    //     name: "AddRenewalInfo",
    //     query: { companyUniqueCode: cUniqueCode }
    //   });
    // },

    getAddRenewalFromSon(companyUniqueCode, renewalDays, renewalTime, version) {
      let postData = {
        companyUniqueCode: companyUniqueCode,
        renewalDays: renewalDays,
        renewalTime: renewalTime,
        version: version
      };
      this.$http
        .post("/user/api/v1/record/insertRenewal", postData)
        .then(response => {
          if (response.success) {
            response.msg = "续费成功";
            this.$store.commit("SET_SNACK", response);
           this.initialize(companyUniqueCode)
          } else {
            this.$store.commit("SET_SNACK", response);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    //编辑回调
    getEditRenewalFromSon(companyUniqueCode) {
      this.initialize(companyUniqueCode);
    },

    // editRenewalInfo(renewalId) {
    //   this.$router.push({
    //     name: "EditRenewalInfo",
    //     query: { renewalId: renewalId }
    //   });
    // },

    // rebackCompany() {
    //   this.$router.push({ path: "companyManage" });
    // },

    deleteOne(id) {
      this.$http
        .post("/user/api/v1/record/deleteRenewal", {
          id: id,
          enable: false
        })
        .then(res1 => {
          if (res1.success) {
              this.initialize(this.cUniqueCode);
              res1.msg = "删除成功";
              this.$store.commit("SET_SNACK", res1);
          } else {
            //删除失败
            this.$store.commit("SET_SNACK", res1);
          }
          return true;
        })
        .catch(err1 => {
          this.$store.commit("SET_SNACK", err1);
        });
    },

    start(cUniqueCode) {
      if (cUniqueCode) {
        this.dialog = true;
        this.initialize(cUniqueCode);
      }
    },

    closeRenewal() {
      this.dialog = false;
    }
  },

  components: {
    DeleteHint: () => import("./component/DeleteHint"),
    AddRenewalInfo: () => import("./component/AddRenewalInfo"),
    EditRenewalInfo: () => import("./component/EditRenewalInfo")
  }

  // beforeRouteLeave(to, from, next) {
  //   alert(to.name +"---"+ from.name)
  //  to.meta.keepAlive = true
  //  next()
  // }
};
</script>